.main-page {
    width: 100%;
    height: 100vh;
    background: url("../assets/img/day/1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display:flex;
    justify-content: center;
    align-items: center;
    color: white;
    transition: 0.3s ease-in-out;
}

.page-container {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.left-container {
    width: 40%;
    height: 100%;
    margin-left: 30px;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: baseline;
}
.left-container p, .right-container p {
    font-size: 40px;
}
.left-container h2 {
    font-size:100px;
}
.right-container h2 {
    font-size: 60px;
}
.right-container {
    width: 40%;
    height: 100%;
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.right-container .button {
    background-color: rgba(61, 60, 60, 0.7);
    width: 140px;
    height:40px;
    border-radius: 50px;
    padding: 5px;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    display:flex;
    justify-content: center;
    align-items: center;
   
}
.right-container .button:hover {
    background-color:  rgba(61, 60, 60, 1);
}


@media (max-width:650px) {
    .page-container {
        flex-direction: column;
    }
    .main-page {

        align-items: center;
        justify-content: center;
    }
    .left-container, .right-container {
        height: 40%;
        width: 90%;
    }
}