.bottom-section {
    width: 100%;
    display: none;
    background-color: rgb(179, 173, 173);
    display: none;
    justify-content: center;
    align-items: center;

}
.bottom-container {
    width: 85%;
    height: 90%;
    border: 1px solid black;
    display: grid;
    grid-template-columns: 0.8fr 0.8fr 1fr;
}



.bottom-container .grid-element p {
    font-size: 35px;
    margin-bottom: 30px;
    color: rgb(44, 43, 43);
}
@media(max-width:650px) {
    .bottom-container {
        grid-template-columns: 1fr;
        border: none;
    }
    body {
        background-color: rgb(179, 173, 173);;
    }
}